* {
  box-sizing: border-box !important;
}

@media (min-width: 550px) {
  .popover-content.sc-ion-popover-ios {
    width: 250px;
  }
}
:root {
  --black-io: #1f1f1f;
  --light-io: #e6e6e9;
  --blue-io: rgb(56, 128, 255);
  --gray: #666;
  --green: #1dc76a;
  --violte: rgb(107, 45, 253);

  --violte-tp: rgba(107, 45, 253, 0.75);
  --red-cris-tp: rgba(255, 63, 56, 0.75);
  --gray-tp: rgba(102, 102, 102, 0.377);
  --gray-tp2: rgba(102, 102, 102, 0.75);
  --blue-io-tp: rgba(56, 128, 255, 0.75);

  --red-cris: rgb(255, 63, 56);
}

ion-toggle {
  transform: scale(0.7);
}

.gbl-bg-principal {
  //background-image: url(/assets/img/fondo-mobile.jpg);
  background-size: cover;
}
.gbl-serach {
  background-color: var(--black-io);
  background: linear-gradient(
    270deg,
    var(--red-cris) 0%,
    rgba(25, 14, 52, 1) 63%,
    rgba(0, 0, 0, 1) 100%
  );
  background: transparent;
  position: sticky;
  top: 0;
  z-index: 1000;
  .searchbar-input.sc-ion-searchbar-md {
    box-shadow: 0px 5px 12px -5px rgba(0, 0, 0, 0.72);
  }
}

.gbl-hiden-700 {
  @include center(column);
}
.gbl-hiden-1200 {
  @include center(column);
}
.gbl-hiden-mobile {
  @include center(column);
}

@media (max-width: 1200px) {
  .gbl-hiden-700 {
    display: flex;
  }
  .gbl-hiden-1200 {
    display: none;
  }
}
@media (max-width: 700px) {
  .gbl-hiden-700 {
    display: none;
  }
  .gbl-hiden-1200 {
    display: none;
  }
}
@media (max-width: 400) {
  .gbl-hiden-700 {
    display: none;
  }
  .gbl-hiden-1200 {
    display: none;
  }
}

@media (min-width: 700px) {
  .gbl-hiden-mobile {
    display: none;
  }
}
