@import "~@swimlane/ngx-datatable/index.css";
@import "~@swimlane/ngx-datatable/themes/material.scss";
@import "~@swimlane/ngx-datatable/assets/icons.css";

.capitalize {
  text-transform: capitalize !important;
}
.datatable-header {
  background-color: #222d32 !important;
  display: flex !important;
}

datatable-footer,
.ngx-datatable.material .datatable-header .datatable-header-cell {
  background: #222d32 !important;

  color: #fff !important;

  font-size: 1rem;

  text-transform: capitalize !important;

  * {
    background: #222d32 !important;

    color: #fff !important;
  }
}

.datatable-body-cell-label {
  font-weight: bold !important;
}

datatable-footer *,
.ngx-datatable.material .datatable-header .datatable-header-cell * {
  color: #fff !important;

  * {
    color: #fff !important;
  }

  li:hover a {
    background: rgb(0, 0, 0) !important;
  }

  .active a {
    background: rgb(27, 126, 238) !important;
  }
}

.ngx-datatable.material.single-selection .datatable-body-row.active,
.ngx-datatable.material.single-selection
  .datatable-body-row.active
  .datatable-row-group,
.ngx-datatable.material.multi-selection .datatable-body-row.active,
.ngx-datatable.material.multi-selection
  .datatable-body-row.active
  .datatable-row-group,
.ngx-datatable.material.multi-click-selection .datatable-body-row.active,
.ngx-datatable.material.multi-click-selection
  .datatable-body-row.active
  .datatable-row-group {
  background-color: #a8acad;
  &:hover {
    background-color: #a8acad;
  }
}

.form-changed {
  * {
    color: var(--green);
    font-weight: bold;
  }
}

.form-invalid {
  * {
    color: var(--red-cris) !important;
    font-weight: 600;
  }
}

::ng-deep {
  .loading-wrapper.ion-overlay-wrapper.sc-ion-loading-md {
    background-color: transparent;
    box-shadow: none;
    * {
      font-weight: bold;
      font-size: 25px;
    }
  }
}

.popover-content.sc-ion-popover-ios {
  --width: 70% !important;
}
.alert-wrapper.sc-ion-alert-ios {
  --width: 90vw !important;
  max-width: 800px;
}
